/* Contenedor principal */
.container {
  padding: 2rem;
  max-width: 800px;
  margin: auto;
}

/* Estilos del formulario */
.form-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: bold;
}

input, select, button {
  padding: 0.5rem;
  font-size: 1rem;
}

button.submit-btn {
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 1rem;
  transition: background-color 0.3s;
}

button.submit-btn:hover {
  background-color: #45a049;
}

.message {
  margin-top: 1rem;
  color: green;
}

/* Filtros */
.filters {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: flex;
  gap: 1rem;
  justify-content: space-between;
}

.filters label {
  font-weight: bold;
}

/* Estilos de la lista de citas */
.appointments-list {
  list-style: none;
  padding-left: 0;
}

.appointments-list li {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border: 1px solid #ddd;
}

button.edit-btn, button.delete-btn {
  padding: 0.3rem 0.6rem;
  margin-left: 0.5rem;
  border: none;
  cursor: pointer;
}

button.edit-btn {
  background-color: #ffa500;
  color: white;
}

button.edit-btn:hover;